@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=Lexend:wght@400&display=swap');

*{
    margin:0;
    padding:0;
}

.App{
    text-align: center;
    background-color: #e6e6e6;
    
} 

.Card{
    box-sizing: border-box;
    border:5px double black;
    border-radius:10px;
    margin:30px;
    width:360px;  
}

.CardH{
    display:flex; 
    justify-content: center;
    border:2px solid pink
}

.CardV{
    display:inline-block; 
    margin:auto auto;
    border:2px solid pink
}

.num{
    font-size:3rem;
    font-weight: bold;
}

.Sitename{
    font-family:Lobster;
}

.caption{
    font-family: Dela Gothic One;
    font-size: 3rem;
    -webkit-text-stroke: 3px black;
}

button{
    padding:20px 40px; 
    font-weight: bold;
    font-size:15px;
    border:2px solid black ;
    border-radius: 5px;
}

button:hover{
    transform: translateY(-5px);
    color:white;
    background-color:#66a3ff;
    cursor:pointer
}